.container {
  max-width: 800px;
  text-align: center;
  margin: 0 auto;
  padding: 20px;
}

.search-form {
  gap: 30px;
  margin-bottom: 20px;
  display: flex;
}

input[type="text"] {
  width: 100%;
  padding: 10px;
  font-size: 16px;
}

button {
  color: #f0f8ff;
  background-color: #434455;
  padding: 10px 20px;
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  display: flex;
}

.photo-card {
  max-width: 300px;
  text-align: center;
  border: 1px solid #ccc;
  padding: 10px;
}

.photo-card img {
  max-width: 100%;
  height: auto;
  margin-bottom: 10px;
}

.load-more {
  margin-top: 20px;
  font-size: 16px;
  display: none;
}

h1 {
  color: #434455;
